.region {
    margin-top: 2rem;
    width: 100%;
}

.region .region_nav {
    position: relative;
    width: 100%;
    height: 130px;
    color: #fff;
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
}

.region_nav h2 {
    position: absolute;
    bottom: 0;
    left: 20px;
    font-size: 34px;
    font-weight: 500;
}

.region_nav b {
    font-size: 20px;
    font-weight: 500;
}

.region ul {
    padding-left: 0;
    margin: 0;
    width: 100%;
    overflow: hidden;
}

.region ul .li {
    position: relative;
    list-style: none;
    width: 100% -2px;
    height: 125px;
    border-left: 6px solid rgb(0, 136, 204);
    border-bottom: 1px solid #00000027;
    border-right: 1px solid #00000027;
}

.region ul .li .img {
    position: absolute;
    width: 64px;
    height: 64px;
    top: 20px;
    left: 20px;
}

.region ul .li .h2 {
    position: absolute;
    top: 20px;
    left: 110px;
    font-size: 24px;
    color: black;
}

.region ul .li p {
    position: absolute;
    top: 45px;
    left: 110px;
    font-size: 14px;
    width: 32%;
}

.region ul .li .tag {
    display: none;
    position: absolute;
    bottom: 8px;
    left: 110px;
}

.region ul .li .list {
    position: absolute;
    width: 500px;
    left: 600px;
}

.region ul .li .topic {
    position: absolute;
    top: 20px;
    left: 500px;
    width: 100px;
    text-align: center;
}